body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html,
body {
  background-color: #1A1A1A;
  color: #CCCCCC;
}

div[role="grid"] {
  outline: none !important;
}

.bg-danger {
  /* background-color: #B13E45 !important; */
  background-color: #171717 !important;
  border-top: 3px solid #B13E45 !important;
}

.text-danger {
  color: #B13E45 !important;
}

.bg-info {
  /* background-color: #3F3F50 !important; */
  background-color: #171717 !important;
  border-top: 3px solid #4D9E4D !important;
}

input,
.form-control,
.card-body,
.footer input.form-control {
  background-color: #212121;
  border: 1px solid #2e2e2e;
  color: #fafafa;
}

.navbar {
  background-color: #171717;
  border-bottom: 1px solid #000000;
}

.header-links i {
  font-size: 120%
}

.nav-link {
  border-top: 2px solid transparent;
  margin-top: -2px;
}

.nav-link.active {
  border-top: 2px solid #356da7;
  background-color: #212121;
}

.navbar a {
  text-decoration: none;
  color: #fafafa;
  font-size: 110%;
  /* text-transform: uppercase; */
  letter-spacing: 1px
}

a {
  color: #64b7d8;
  text-decoration: underline;
}

.navbar-brand:hover,
.nav-link:hover {
  background-color: #383838;
}

.form-control:focus {
  background-color: #171717 !important
}

.footer input.form-control {
  width: auto;
  width: initial;
  display: inline;
  padding: 2px 4px 2px 4px;
}

.CoinCard {
  box-sizing: content-box;
  background-color: transparent;
  border: none;
}

/* select, option {
  text-transform: uppercase;
} */
* {
  /* font-family: 'Lato', monospace !important; */
  font-family: 'Lato', Arial, Helvetica, sans-serif;
  font-size: 15px;
}

.CoinCard h3 {
  margin-bottom: 0;
}

.CoinCard .card-text>p:last-child {
  margin-bottom: 0;
}

/* .CoinCard:hover .card-body {
  border-color: #555;
} */
/* .card-header:hover a:hover {
  border-left-color: #555;
  border-right-color: #555;
  background-color: #212121 !important;
} */
.CoinCard a.header-link {
  text-decoration: none !important;
  color: #ccc;
}

.CoinCard a:hover {
  text-decoration: underline !important;
}

.card-header i {
  font-size: 190%;
  color: #356da7
}

.card-header span {
  line-height: 60px;
}

/* .header-link {
  color: #ccc !;
} */
.btn-info {
  background-color: #356da7 !important;
  color: #fff !important;
  border-color: #356da7;
}

.card-header {
  border: 1px solid #2e2e2e;
  border-bottom: none;
  height: 77.5px;
  color: #ccc;
}

.card-header h3 {
  font-size: 190%;
  height: 50px;
  background-repeat: no-repeat;
  background-size: 50px 50px;
  padding-left: 60px;
  line-height: 50px;
}

.card-header img {
  vertical-align: middle
}

.fa-bars {
  color: #ccc;
}

.form-control:focus {
  background-color: #555;
  color: #ccc;
  outline: none !important;
  box-shadow: none !important;
}

/* * {
  font-family: 'Roboto Mono script=latin rev=3', monospace !important;
  font-size: 15px;
  line-height: 20px;
} */
/* .asic-friendly {
  background: linear-gradient(to right, rgba(255,255,255,1) 0%,rgba(255,255,255,0.9) 100%),url(data:image/gif;base64,) repeat -35px -30px;
} */
.card {
  width: 100%;
  border: none;
  padding-left: 15px;
  padding-top: 15px;
}

.active-btc {
  color: #D7BA7D
}

.active-usd {
  color: #4D9E4D
}

.active-coins {
  color: #D4D4D4
}

.tab-pane,
.coinlist {
  padding-right: 15px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.btn {
  font-size: 100%;
  text-transform: uppercase;
}

.btn:focus {
  box-shadow: none !important;
}

.btn-primary {
  background-color: #233c59 !important;
  /* border-color: #356da7 !important; */
  border-color: #233c59 !important;
  margin-right: 0 !important;
}

.btn-secondary {
  background-color: transparent !important;
  color: #ccc;
  border-color: #2e2e2e;
  margin-right: 0 !important;
}

.card-header:first-child {
  border-radius: 0;
}

.logoName {
  border-radius: 50%;
  margin-right: 10px;
}

.card .inner {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.card-body {
  background-color: #212121
}

@media (min-width: 700px) {
  .card {
    width: 100%;
  }
}

@media (min-width: 900px) {
  .card {
    width: 50%;
  }
}

@media (min-width: 1200px) {
  .card {
    width: 33%;
  }
}

@media (min-width: 1500px) {
  .card {
    width: 25%;
  }
}

.hide {
  display: none
}

.nav-item {
  padding-right: 5px;
}

.input-group-text {
  background-color: #433c2c;
  background-color: #502850;
  border: none;
  color: #fafafa
}

.nav-link {
  padding-right: .5rem !important;
  padding-left: .5rem !important;
}

.navbar-expand-lg .navbar-nav {
  flex-direction: row;
}

.text-danger {
  font-size: 120%;
}

.asic-friendly {
  background: linear-gradient(to right, rgba(33, 33, 33, 1) 0%, rgba(33, 33, 33, 0.4) 100%), url(data:image/png;base64,) repeat 15px -30px;
}

